      self.loading = true;
      if (self.student) {
        switch (self.type) {
          case 1:
            var number = self.student;
            API.searchName(val, self.size, name, number)
              .then((res) => {
                self.loading = false;
                self.$message.success("");
                self.tableData = res.data;
