    },
    search() {
      var self = this;
      self.loading = true;
      if (!self.type) {
        self.$message.error("");
        self.loading = false;
      } else {
        if (self.type == 1) {
          self.current = 1;
          var number = self.student;
          API.searchName(self.current, self.size, name, number).then((res) => {
            self.loading = false;
            self.$message.success("");
            self.tableData = res.data;
            self.total = res.total;
          });
