            loading.close();
          });
      } else {
        self.$message.error(", !");
      }
    },

    // 
    handlePushFace(index, row) {
      var self = this;
      console.log(row);
      if (row.school) {
        if (row.school.offline == 1) {
          self.hasUuid = true;
        }
      }
      self.dialogPushFace = true;
      self.openFace_id = row.id;
      if (row.face_id && row.face_image) {
        self.hasFace = true;
      } else {
        self.hasFace = false;
      }
    },
    inputUuid(value) {
      console.log(value);
      var self = this;
      self.uuid = value;
    },
    pushFace() {
      var self = this;
      var student = "student";

      if (self.uuid) {
        if (self.hasFace) {
          const loading = self.$loading({
            lock: true,
            text: "...",
            spinner: "el-icon-loading",
            background: "rgba(0, 0, 0, 0.7)",
          });
          API.pushFace1(self.openFace_id, student, self.uuid)
            .then((res) => {
              self.dialogPushFace = false;
              self.uuid = "";
              loading.close();
              self.$message.success("");
            })
            .catch((err) => {
              loading.close();
            });
        } else {
          self.$message.error(", !");
        }
      } else {
        if (self.hasFace) {
          const loading = self.$loading({
            lock: true,
            text: "...",
            spinner: "el-icon-loading",
            background: "rgba(0, 0, 0, 0.7)",
          });
          API.pushFace(self.openFace_id, student)
            .then((res) => {
              self.dialogPushFace = false;
              loading.close();
              self.$message.success("");
            })
            .catch((err) => {
              loading.close();
            });
        } else {
          self.$message.error(", !");
        }
      }
    },

    // 
    handleCreateFace(index, row) {
      var self = this;
      self.id = row.id;
      self.dialogCreateFace = true;
      console.log(row);
      if (row.face_id && row.face_image) {
        self.hasFace = true;
      } else {
        self.hasFace = false;
      }
    },
    createFace() {
      var self = this;
      if (self.hasFace) {
        const loading = self.$loading({
          lock: true,
          text: "...",
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.7)",
        });
        API.createFace(self.id)
          .then((res) => {
            self.dialogCreateFace = false;
            loading.close();
            self.$message.success("");
          })
          .catch((err) => {
            loading.close();
          });
      } else {
        self.$message.error(", !");
      }
    },

    // 
    handleServe(index, row) {
      var self = this;
      self.id = row.id;
      self.dialogOpenedServe = true;
      API.studentServe(self.id).then((res) => {
        console.log(res);
        self.$message.success("");
        self.serviceList = res;
      });
    },

    // 
    handleEdit(index, row) {
      var self = this;
      // self.getStudent();
      self.dialogStudent = true;
      row.sex = Number(row.sex);
      self.form = row;
      self.grade = row.grade_id;
      self.classValue = row.class;
    },

    // 
    handleDel(index, row) {
      var self = this;
      self.dialogDel = true;
      self.id = row.id;
    },
    toDel() {
      var self = this;
      API.delStudent(self.id).then((res) => {
        self.$message.success("");
        self.dialogDel = false;
        self.getStudent();
        self.current = 1;
      });
    },

    // 
    handleFaceLogs(index, row) {
      var self = this;
      self.dialogFaceLogs = true;
      self.log_id = row.id;
      self.logsCurrent = 1;
      self.logsSize = 10;
      API.faceLogs(self.logsCurrent, self.logsSize, self.log_id).then((res) => {
        self.$message.success("!");
        self.FaceLogsDate = res.data;
        self.logsTotal = res.total;
        self.FaceLogsDate.forEach((item) => {
          item.timestamp = DATE.formatTime(item.timestamp, "Y-M-D h:m:s");
        });
      });
    },
    // 
    logsCurrentChange(val) {
      var self = this;
      self.logsCurrent = val;
      API.faceLogs(val, self.logsSize, self.log_id).then((res) => {
        self.FaceLogsDate = res.data;
        self.logsTotal = res.total;
        self.FaceLogsDate.forEach((item) => {
          item.timestamp = DATE.formatTime(item.timestamp, "Y-M-D h:m:s");
        });
      });
    },
    logSizeChange(val) {
      var self = this;
      self.logsSize = val;
      API.faceLogs(self.logsCurrent, val, self.log_id).then((res) => {
        self.FaceLogsDate = res.data;
        self.logsTotal = res.total;
        self.FaceLogsDate.forEach((item) => {
          item.timestamp = DATE.formatTime(item.timestamp, "Y-M-D h:m:s");
        });
      });
    },

    // 
    handleChange(file) {
      var self = this;
      self.change_href = URL.createObjectURL(file.raw);
      self.hasNewImage = true;
    },
    handleRemove(file) {
      var self = this;
      self.change_href = "";
      self.hasNewImage = false;
    },
    beforeAvatarUpload(file) {
      var self = this;
      // self.familyForm.href = file.name;
      self.fileName = md5(file.name);
      self.suffix = file.name.substring(file.name.lastIndexOf(".") + 1);
      self.imgData.key = `tmp_${self.fileName}.${self.suffix}`;
    },
    changeFace() {
      var self = this;
      if (self.change_href === "") {
        self.familyForm.href = self.old_href;
        API.studentFace(self.familyForm).then((res) => {
          self.$message.success("");
          self.current = 1;
          self.getStudent();
          self.familyForm.href = "";
          self.dialogFace = false;
        });
      } else {
        this.$refs.upload.submit();
      }
    },
    handleAvatarSuccess(res, file) {
      var self = this;
      file.url = `${self.qiniuaddr}/${res.key}`;
      self.familyForm.href = file.url;
      API.studentFace(self.familyForm).then((res) => {
        self.$message.success("");
        self.current = 1;
        self.getStudent();
        self.$refs.upload.clearFiles();
        self.familyForm.href = "";
        self.change_href = "";
        self.old_href = "";
        self.imgData.key = "";
        self.familyForm.user_id = "";
        self.dialogFace = false;
      });
    },
    handleExceed(file, fileList) {
      //
      var self = this;
      self.$message.error("1!");
      self.$refs.upload.clearFiles();
      self.familyForm.href = "";
      self.imgData.key = "";
      self.familyForm.user_id = "";
    },
    getQiniuToken() {
      var self = this;
      axios
        .get("https://api.fengniaotuangou.cn/api/upload/token")
        .then((res) => {
          self.imgData.token = res.data.uptoken;
        });
    },
  },
};
</script>

